import {Component} from "react"
import {chan, stateChan} from "./chan"
import connect from "./connect"
import {store, derive, history} from "./store"

// User intents
let intents = {
  addTodo: chan(),
  toggleTodo: chan(),
  setFilter: chan(),
  undo: chan(),
  redo: chan(),
}

// State actions
let stateLoop = stateChan()

// Update current state (<currentState>)
let actions = {
  addTodo: intents.addTodo.withLatestFrom(stateLoop, (text, state) => {
    return (state) => {
      let id = String(Object.values(state.todos).length + 1)
      return R.assocPath(["todos", id], {
        id,
        text,
        completed: false,
        addedAt: new Date().toISOString(),
      }, state)
    }
  }),

  toggleTodo: intents.toggleTodo.map(id => R.overX(["todos", id, "completed"], x => !x)),

  setFilter: intents.setFilter.map(filter => R.assoc("filter", filter)),
}

// Update state in historical context ([... <currentState>])
let historyActions = {
  undo: intents.undo.map(() => stateHistory => {
    return R.assoc("i", stateHistory.i - 1, stateHistory)
  }),
  redo: intents.redo.map(() => stateHistory => {
    return R.assoc("i", stateHistory.i + 1, stateHistory)
  }),
}

// State stream
let initialState = {
  todos: { // it's more convenient to have an object of models than an array of them, in general
    "1": {
      id: "1",
      text: "Write a TODO",
      completed: false,
      addedAt: new Date().toISOString(),
    }
  },
  filter: "all",
}

let state = history(store(initialState, actions), 3, historyActions, (hs) => {
  console.log("== HS SNAPSHOT ==")
  console.log("log:", hs.log)
  console.log("i:", hs.i)
  stateLoop.next(hs.log[hs.i])
  console.log()
})

let derived = {
  filteredTodos: derive(state, (state) => {
    switch (state.filter) {
      case "all":
        return Object.values(state.todos)
      case "completed":
        return R.sortBy(t => t.addedAt, R.filter(t => t.completed, Object.values(state.todos)))
      case "active":
        return R.sortBy(t => t.addedAt, R.filter(t => !t.completed, Object.values(state.todos)))
      default:
        throw Error("Unknown filter: " + state.filter)
    }
  })
}

// Rendering & Events
let AddTodo = (props) => {
  let input
  return <div>
    <form onSubmit={e => {
      e.preventDefault()
      if (!input.value.trim()) {
        return
      }
      intents.addTodo(input.value)
      input.value = ""
    }}>
      <input ref={node => {
        input = node
      }} />
      <button type="submit">
        Add Todo
      </button>
    </form>
  </div>
}

let TodoItem = (props) =>
  <li
    onClick={() => intents.toggleTodo(props.todo.id)}
    style={{textDecoration: props.todo.completed ? "line-through" : "none"}}
  >
    {props.todo.text}
  </li>

let TodoList = connect(
  {todos: derived.filteredTodos},
  (props) =>
    <ul>
      {props.todos.map(todo =>
        <TodoItem key={todo.id} todo={todo}/>
      )}
    </ul>
)

let Footer = (props) =>
  <p>
    Show:
    {" "}
    <a id="all" href="#all" onClick={(e) => { e.preventDefault(); intents.setFilter("all"); }}>
      All
    </a>
    {", "}
    <a id="active" href="#active" onClick={(e) => { e.preventDefault(); intents.setFilter("active"); }}>
      Active
    </a>
    {", "}
    <a id="completed" href="#completed" onClick={(e) => { e.preventDefault(); intents.setFilter("completed"); }}>
      Completed
    </a>
  </p>

let UndoRedo = (props) =>
  <div>
    <button onClick={intents.undo()}>Undo</button>
    {" "}
    <button onClick={intents.redo()}>Redo</button>
  </div>

let App = (props) =>
  <div>
    <AddTodo/>
    <TodoList/>
    <Footer/>
    <UndoRedo/>
  </div>

ReactDOM.render(<App/>, document.getElementById("root"))



