let noop = (x) => null

// (State, Object (Observable (State -> State)), () -> null) -> Observable State
export let store = (initialState, actions, spyFn=noop) => {
  actions = Object.values(actions) // converts objects, leaves arrays untouched
  return Observable.merge(...actions)
   .startWith(initialState)
   .scan((state, fn) => {
      if (typeof fn != "function") {
        throw Error(`invalid fn ${fn} dispatched`)
      } else {
        return fn(state)
      }
   })
   .distinctUntilChanged(R.equals)
   .do(spyFn)
   .shareReplay(1)
}

// Observable State, (State -> State) -> Observable State
export let derive = (state, deriveFn) => {
  return state
    .map(deriveFn)
    .distinctUntilChanged()
    .shareReplay(1)
}

// (Observable State, Number, Object (Observable (State -> State)), () -> null) -> Observable State
export let history = (state, historyLength, actions, spyFn) => {
  actions = Object.values(actions) // converts objects, leaves arrays untouched
  let initialState = {
    log: normalizeLog([], historyLength, historyLength - 1), // [null, ... <state>]
    i: historyLength - 1,                                    //            5
  }
  return Observable.merge(
    Observable.merge(...actions),
    state.map(newState => stateHistory => {
      // any basic action resets Redo option
      return {
        log: normalizeLog([...R.slice(0, stateHistory.i + 1, stateHistory.log), newState], historyLength),
        i: historyLength - 1,
      }
    }),
  )
    .scan((stateHistory, fn) => fn(stateHistory), initialState)
    .distinctUntilChanged() // a weak help but...
    .do(spyFn)
    .shareReplay(1)
}

// Array State, Number -> Array State
let normalizeLog = (xs, len) => {
  return R.takeLast(len, [...R.repeat(null, len), ...xs])
}




